.menu {
  display: flex;
  flex-direction: column;
  align-items: start;
  color: var(--stone4);
  border-radius: 1rem;
  background: var(--white);
  width: 49.375rem;
  padding: 2.75rem;
}
.menu img {
  align-self: center;
  width: 43.75rem;
  border-radius: 1rem;
  margin-bottom: 2.5rem;
}
.menu p {
  margin-bottom: 1.875rem;
}
.menu__card {
  background: var(--rose1);
  margin-bottom: 1.875rem;
  width: 43.875rem;
  padding: 1.875rem;
}
.menu h2 {
  color: var(--brown);
}
.menu h3 {
  color: var(--rose2);
}
.menu ul, .menu ol {
  list-style-position: outside; /* Position the marker outside */
  padding-left: 1.25rem; /* Adjust this value as needed */
}
.menu li {
  line-height: 1.3;
  margin-bottom: 0.625rem; /* Adjust this value as needed */
  text-indent: 0; /* No negative indent needed */
  padding-left: 1.25rem; /* Adjust padding to control space between marker and text */
}
.menu__nutrition {
  display: flex;
  justify-content: space-between; /* Distribute space between items */
  margin-left: 1.25rem;
  width: 50%;
  align-items: center; /* Align items vertically centered */
  padding: 0.3125rem 0;
}
.menu__nutrition__color1 {
  color: var(--stone3);
}
.menu__nutrition__color2 {
  font-weight: 700;
  color: var(--brown);
}
.menu__nutrition span:nth-child(1) {
  flex-grow: 1; /* Allow the first span to take up available space */
}

/* Media query for screens smaller than 798 pixels */
@media (max-width: 49.875em) {
  .menu {
    position: relative;
    top: -31px;
    display: flex;
    align-items: start;
    justify-content: center;
    padding: 1.875rem;
    height: 100%;
    width: 100%;
    box-sizing: border-box; /* Ensure padding is included in the width */
  }
  .menu img {
    top: 1.25rem;
    min-height: auto;
    max-width: auto;
  }
  .menu__card {
    margin: auto 1.875rem 1.875rem 0.625rem;
    width: 96%;
    background-size: auto;
  }
  .menu__nutrition {
    width: 70%;
  }
}
ul.custom-list {
  list-style-type: disc;
}

ul.custom-list li::marker {
  color: var(--brown); /* Change the color of the bullet */
}

ol.custom-list {
  counter-reset: item; /* Initialize the counter */
  list-style: none; /* Remove the default list style */
}

ol.custom-list li {
  counter-increment: item; /* Increment the counter */
  position: relative; /* Relative position for absolute positioning of number */
}

ol.custom-list li::before {
  content: counter(item) ". "; /* Display the counter value */
  color: var(--brown); /* Change the color of the number */
  position: absolute; /* Position it absolutely */
  left: -30px; /* Adjust based on your preference */
}

html {
  font-size: 100%;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  font-family: "Outfit", sans-serif;
  margin: 1.875rem 6.25rem 1.875rem 6.25rem;
  padding: 0;
  height: 100%;
  background: var(--stone1);
}
@media (max-width: 49.875em) {
  body {
    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin: 0;
    padding: auto 1.875rem 1.875rem 1.875rem; /* Add padding for content */
    background: var(--white);
  }
}
body footer {
  font-size: 18.75rem;
  margin-top: 1.25rem;
  margin-bottom: 1.25rem;
}

:root {
  --white: hsl(0, 0%, 100%);
  --stone1: hsl(30, 54%, 90%);
  --stone2: hsl(30, 18%, 87%);
  --stone3: hsl(30, 10%, 34%);
  --stone4: hsl(24, 5%, 18%);
  --brown: hsl(14, 45%, 36%);
  --rose1: hsl(330, 100%, 98%);
  --rose2: hsl(332, 51%, 32%);
}

h1,
h2,
h3 {
  font-family: "Young Serif", sans-serif;
  margin-top: 0;
  margin-bottom: 0.625rem;
  line-height: 1.1;
}

a,
a:visited,
a:hover {
  cursor: pointer;
}

p {
  font-size: 1rem;
}

hr {
  margin-bottom: 1.875rem;
  width: 43.875rem;
  border: 1px solid var(--stone1);
}